const util = {
  /**
   * 
   * @param url
   */
  checkUrl: function (url) {
    var regexp = new RegExp("(http[s]{0,1}|ftp)://[a-zA-Z0-9\\.\\-]+\\.([a-zA-Z]{2,4})(:\\d+)?(/[a-zA-Z0-9\\.\\-~!@#$%^&*+?:_/=<>]*)?", "gi");
    return regexp.test(url);
  },
  /**
   * 
   * @param date
   * @returns {boolean}
   */
  isDate: function (date) {
    date = new Date(date);
    return date instanceof Date && !isNaN(date.valueOf());
  },
  /**
   * 
   * @param date
   * @returns {string}
   */
  formatDate: function (date) {
    date = new Date(date);
    if (util.isDate(date)) {
      return date.getFullYear() + '/' + (parseInt(date.getMonth(), 10) + 1) + '/' + date.getDate();
    } else {
      console.log('');
    }
  },
  /**
   * 
   * @param date
   * @param type
   * @param days
   * @returns {*|string}
   */
  changeDate: function (date, type, days) {
    date = new Date(date);
    if (!util.isDate(date)) {
      console.log('');
      return false;
    }
    if (type === 'add') {
      date = date.setDate(date.getDate() + days);
    } else if (type === 'subtract') {
      date = date.setDate(date.getDate() - days);
    }
    return util.formatDate(date);
  },
}

export default util



